<template>
    <div class="main__content">
        <div class="main__content-left">
            <div class="main__content-degree"> +{{ Math.round(weather.current.temp) }}</div>
            <div class="main__content-today">Сегодня</div>
            <div class="main__content-time">Время: {{ getTime }} </div>
            <div class="main__content-city">Город: {{ weather.name }}</div>

            <div class="main__content-left-img">
                <img :src="getIcon" alt="sun">
                <div class="main__content-weather">{{ weather.current.weather[0].description }}</div>
            </div>
        </div>
        <div class="main__content-right">
            <img src="@/assets/img/cloud.svg" alt="cloud" class="main__content-right-bg">
            <div class="main__content-right-item">
                <div class="main__content-right-img">
                    <img src="@/assets/img/temp.svg" alt="">
                </div>
                <span>Температура:</span>
                <p>+{{ Math.round(weather.current.temp) }} - ощущается как +{{ Math.round(weather.current.feels_like) }}</p>

            </div>
            <div class="main__content-right-item">
                <div class="main__content-right-img">
                    <img src="@/assets/img/pressure.svg" alt="">
                </div>
                <span>Давление:</span>
                <p>{{ Math.round(weather.current.pressure * 0.750062) }} мм ртутного столба - {{
                    Math.round(weather.current.pressure * 0.750062) >= 760 ? 'высокое' : 'нормальное' }}</p>

            </div>
            <div class="main__content-right-item">
                <div class="main__content-right-img">
                    <img src="@/assets/img/precipation.svg" alt="">
                </div>
                <span>Осадки:</span>
                <p>Вероятность осадков - {{ weather.current.clouds }}%</p>

            </div>
            <div class="main__content-right-item">
                <div class="main__content-right-img">
                    <img src="@/assets/img/wind.svg" alt="">
                </div>
                <span>Ветер:</span>
                <p>{{ Math.round(weather.current.wind_speed) }} м/с - {{ getWind }}</p>

            </div>
        </div>
    </div>
</template>

<script>
import { mapState } from 'vuex';
import { weatherIcon } from '@/icon';
export default {

    computed: {
        ...mapState(['weather']),
        ...mapState(['icon']),
        description() {
            return this.weather.current.weather[0].description
        },
        getWind() {
            if (this.weather.current.wind_deg == 0) {
                return 'север'
            } else if (0 < this.weather.current.wind_deg && this.weather.current.wind_deg < 90) {
                return 'северо-восток'
            } else if (this.weather.current.wind_deg == 90) {
                return 'восток'
            } else if (90 < this.weather.current.wind_deg && this.weather.current.wind_deg < 180) {
                return 'юго-восток'
            } else if (this.weather.current.wind_deg == 180) {
                return 'юг'
            } else if (180 < this.weather.current.wind_deg && this.weather.current.wind_deg < 270) {
                return 'юго-запад'
            } else if (this.weather.current.wind_deg == 270) {
                return 'запад'
            } else {
                return 'северо-запад'
            }
        },
        getIcon() {
            return `https://openweathermap.org/img/wn/${this.weather.current.weather[0].icon}@2x.png`
        },
        getImg() {
            return weatherIcon[this.description] || weatherIcon['ясно']
        },
        getTime() {
            return new Date().toLocaleString('en-US', {
                timeZone: this.weather.timezone,
                hourCycle: 'h23',
                timeStyle: 'short',
            })
        }
    },
}
</script>

<style></style>