<template>
  <header class="header">
    <nav class="header__nav">
      <a href="" class="logo">
        <img src="@/assets/img/logo.svg" alt="">
        <span class="logo__span">vue weather</span>
      </a>
      <div class="header__search">
        <p>Theme <span>&rarr;</span></p>
        <img @click="darkMode" src="@/assets/img/nav.svg" alt="">
        <input @keydown.enter="getWeather(city)" v-model="city" type="text" placeholder="Выбрать Город"
          class="header__search-input">
      </div>
    </nav>
  </header>
</template>

<script>
import { mapState, mapActions } from "vuex";
export default {
  methods: {
    ...mapActions(['getWeather']),
    darkMode() {
      let value = document.body.style.getPropertyValue('--bg')
      if (value == 'white') {
        document.body.style.setProperty('--bg', 'rgb(33, 32, 32)')
        document.body.style.setProperty('--text', '#4793ff')
      } else {
        document.body.style.setProperty('--bg', 'white')
        document.body.style.setProperty('--text', 'black')
      }
    }
  },
  data() {
    return {
      city: ''
    }
  },
  created() {
    document.body.style.setProperty('--bg', 'white')
    document.body.style.setProperty('--text', 'black')
  }
}
</script>

<style>
.header__search img {
  cursor: pointer;
}
</style>