import { createStore } from "vuex";
import axios, { Axios } from "axios";

const store = createStore({
    state: {
        apiKey: "a6fbb52b2f2bc00b4cf4aaa2411f4e48",
        weather: null,
        // icon: null,
    },

    mutations: {
        getWeather(state, payload) {
            state.weather = payload;
        },
        // getIcon(state, payload) {
        //     state.icon = payload;
        // },
    },

    actions: {
        async getWeather({ commit, state }, city) {
            try {
                let response = await axios.get(
                    `http://api.openweathermap.org/geo/1.0/direct?q=${city}&appid=${state.apiKey}`
                );
                let { lat, lon, local_names } = response.data[0];

                let response2 = await axios.get(
                    `https://api.openweathermap.org/data/2.8/onecall?lat=${lat}&lon=${lon}&exclude=minutely,hourly,alerts&appid=${state.apiKey}&units=metric&lang=ru`
                );
                let weatherInfo = { ...response2.data, name: city };
                commit("getWeather", weatherInfo);
            } catch (error) {
                console.log(error);
            }
        },

        // async getIcon({ commit }, icon) {
        //     try {
        //         let response = await axios.get(
        //             `https://openweathermap.org/img/wn/${icon}@2x.png`
        //         );
        //         console.log(response);
        //         commit("getIcon");
        //     } catch (error) {
        //         console.log("Error occured", error);
        //     }
        // },
    },

    getters: {},
});
export default store;
